body,
html {
    height: 100%;
    margin: 0;
    padding: 0;
    width: 100%;
}
.todo-details {
    display: flex;
    flex-direction: column;

    header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0.5em 1em;
    }

    .item {
        flex: 1;
        padding: 1em;
    }
}

.todo {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin: 2em;

    form {
        display: flex;
        flex-direction: column;
        margin-right: 2em;

        label {
            display: flex;
            flex-direction: column;
            margin-bottom: 1em;
        }
    }

    ul {
        list-style-type: none;
        margin: 0;
        padding: 0;

        li {
            align-items: center;
            display: flex;
            flex-direction: row;
            font-size: 0.75em;
            margin: 0.75em 0.5em;

            &.complete-true {
                span {
                    background: pink;
                    text-decoration: line-through;
                    &:hover {
                        background: seagreen;
                    }
                }
            }

            span {
                background: #e0e0e0;
                cursor: pointer;
                &:hover {
                    background: pink;
                }
            }
            span,
            input {
                border: 0.5px solid #eee;
                display: inline-block;
                padding: 0.2em 0.25em;
                width: 300px;
            }

            button {
                background: transparent;
                border: none;
                color: blue;
                cursor: pointer;
                padding: 0;
                margin: 0 0.75em;
            }
        }
    }
}
.board-color {
    background-color: #f1d4d4;
}
section {
    margin-top: 16vh !important;
}
